import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmCurrentTime,
  VmTime 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-current-time', VmCurrentTime);
define('vm-time', VmTime);
  

export interface CurrentTimeProps extends JSX.VmCurrentTime, HTMLAttributes<HTMLVmCurrentTimeElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmCurrentTimeElement, CurrentTimeProps>(
  'vm-current-time',
  new Set(['currentTime','i18n','alwaysShowHours']),
);
  